// HTML разметка бургер меню
@mixin burger {
  .burger {
    position: relative;
    border: none;
    padding: 0;
    width: 30px;
    height: 20px;
    color: #000;
    background-color: transparent;
    cursor: pointer;

    &::before,
    &::after {
      content: "";
      position: absolute;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: currentColor;
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }

    &::before {
      top: 0;
    }

    &::after {
      top: calc(100% - 2px);
    }

    &__line {
      position: absolute;
      left: 0;
      top: 50%;
      width: 100%;
      height: 2px;
      background-color: currentColor;
      transform: translateY(-50%);
      transition: transform 0.3s ease-in-out;
    }

    &--active {
      &::before {
        top: 50%;
        transform: rotate(45deg);
        transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
      }

      &::after {
        top: 50%;
        transform: rotate(-45deg);
        transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
      }

      .burger__line {
        transform: scale(0);
        transition: transform 0.3s ease-in-out;
      }
    }
  }
}